.container {
  width: 100%;
  margin: 0 auto;
  position: relative;
  /* height: 100%; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.wave {
  width: 100%;
  min-height: 500px;
  position: relative;
  background: -webkit-gradient(linear, left top, right top, from(#f0027f), to(#75489f));
  background: -webkit-linear-gradient(left, #f0027f, #75489f);
  background: -o-linear-gradient(left, #f0027f, #75489f);
  background: linear-gradient(90deg, #f0027f, #75489f);
  /* transition: 0.5s; */
  /* background: #ffc728; */
}
.wave:after {
  position: absolute;
  width: 100%;
  height: 100%;
  content: '';
  background: inherit;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  -webkit-transform-origin: top right;
      -ms-transform-origin: top right;
          transform-origin: top right;
  -webkit-transform: skewY(-4deg);
      -ms-transform: skewY(-4deg);
          transform: skewY(-4deg);
}

.wave1 {
  width: 100%;
  min-height: 500px;
  position: relative;
  margin-bottom: 100px;
  background: #ffc728;
  top: 150px;
  /* border: 1px solid red; */
}
.wave1:after {
  position: absolute;
  width: 100%;
  height: 100%;
  content: '';
  background: #ffc728;
  z-index: -1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  -webkit-transform-origin: left top;
      -ms-transform-origin: left top;
          transform-origin: left top;
  -webkit-transform: skewY(-4deg);
      -ms-transform: skewY(-4deg);
          transform: skewY(-4deg);
}



