.container {
  overflow-y: auto;
  height: calc(100vh - var(--nav-height));
}
.content {
  z-index: 100;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

@media screen and (max-width: 660px) {
  .content {
    top: 50%;
  }
}



