.container {
  /* transform: scale(0.8); */
}
.card {
  position: absolute;
  top: -40%;
  left: 10%;
  /* transform: translate(-50%, -50%); */
  width: 300px;
  min-height: 400px;
  background: white;
  -webkit-box-shadow: 0 2px 50px 10px rgba(0, 0, 0, 0.3);
          box-shadow: 0 2px 50px 10px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
.card:hover {
  -webkit-box-shadow: 0 2px 50px 10px rgba(0, 0, 0, 0.5);
          box-shadow: 0 2px 50px 10px rgba(0, 0, 0, 0.5);
}

.card .box {
  position: absolute;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  text-align: center;
  padding: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding-top: 14%;
  padding-bottom: 10%;
}
.card .box .img {
  border: 2px solid black;
  font-size: 120px;
  width: 120px;
  height: 120px;
  margin: 0 auto;
  border-radius: 50%;
  overflow: hidden;
}
.card .box .img img {
  width: 100%;
  height: 100%;
}
.card .box h2 {
  font-size: 20px;
  color: darken;
}
.card .box h2 span {
  font-size: 14px;
  background: lightcoral;
  color: white;
  display: inline-block;
  padding: 4px 10px;
  border-radius: 15px;
}
.card .box p {
  color: grey;
}
.card .box ul {
  margin: 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  /* border: 1px solid red; */
}
.card .box ul li {
  list-style: none;
  /* border: 1px solid red; */
}
.card .box ul li:hover {
  cursor: pointer;
}
.card .box ul li a {
  display: block;
  color: lightgray;
  margin: 0 10px;
  font-size: 20px;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  text-align: center;
}
.card .box ul li:hover a {
  cursor: pointer;
  pointer-events: none;
  color: lightpink;
  -webkit-transform: rotateY(360deg);
          transform: rotateY(360deg);
}

@media screen and (max-width: 660px) {
  .card {
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
  }
}



