.container {
  margin: 0 auto;
  position: absolute;
  right: 20%;
  bottom: 4%;
  /* height: 24px;
  width: 45px;
  border: 1px solid red; */
  z-index: 100;
  -webkit-transform: scale(2);
      -ms-transform: scale(2);
          transform: scale(2);
}

.toggle {
  background: white;
  position: absolute;
  top: 2px;
  border: 2px solid black;
  height: 24px;
  width: 45px;
  cursor: pointer;
  border-radius: 20px;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  -webkit-box-shadow: 0 5px 15px 2px rgba(0, 0, 0, 0.5);
          box-shadow: 0 5px 15px 2px rgba(0, 0, 0, 0.5);
}
.toggle:before {
  content: '';
  position: absolute;
  top: 1px;
  left: 2px;
  width: 18px;
  height: 18px;
  background: #00151f;
  border-radius: 50%;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
.toggle.active:before {
  left: 21px;
}

.night {
  background: #00151f;
}
.font {
  color: white;
}



